.intro {
  margin-top: 60px;
  height: 640px;
  @include r(767) {
    margin-top: 25px;
    height: 655px; }
  h4 {
    font-size: 30px;
    font-weight: 500;
    letter-spacing: 0.8px;
    color: $charcoal-grey-three;
    @include r(1024) {
      font-size: 22px; }
    @include r(767) {
      font-size: 20px; } }
  h2 {
    font-weight: bold;
    font-size: 80px;
    letter-spacing: normal;
    color: $lipstick;
    text-transform: uppercase;
    @include r(1024) {
      font-size: 56px; }
    @include r(767) {
      font-size: 42px; }
    &.title-scondary {
      font-size: 70px;
      @include r(1024) {
        font-size: 56px; }
      @include r(767) {
        font-size: 30px; } } }
  h3 {
    font-size: 56px;
    color: $dark-two;
    letter-spacing: -0.6px;
    text-transform: uppercase;
    @include r(1024) {
      font-size: 32px; }
    @include r(767) {
      font-size: 24px; }
    &.title-scondary {
      font-size: 55px;
      color: $lipstick;
      @include r(1024) {
        font-size: 32px; }
      @include r(767) {
        font-size: 24px; } } }
  b {
    font-size: 36px;
    color: $dark-two;
    letter-spacing: -0.6px;
    text-transform: uppercase;
    margin-top: 20px;
    @include r(1024) {
      font-size: 30px; }
    @include r(767) {
      font-size: 20px; } }
  p {
    font-size: 15px;
    line-height: 1.47;
    letter-spacing: 0.4px;
    text-align: left;
    color: $charcoal-grey;
    max-width: 410px;
    margin-bottom: 50px;
    margin-top: 30px;
    @include r(767) {
      padding: 0 40px;
      font-size: 12px;
      text-align: center;
      position: relative;
      left: 50%;
      transform: translate(-50%); } }
  .swiper-slide {
    padding: 20px 0;
    background-repeat: no-repeat;
    background-size: 40%;
    background-position: 100% 50%;
    @include r(767) {
      background-position: 50% 77%;
      .container {
        padding: 0; } }
    @include r(560) {
      background-size: 70%; }
    img {
      max-width: 440px;
      width: 100%;
      @include r(767) {
        padding: 0 20px;
        padding-top: 50px; } } }
  .swiper-pagination {
    margin-bottom: 20px; }
  .swiper-container {
    background-repeat: no-repeat;
    background-size: cover; } }
