  width: 6px;
  height: 6px;
  background: #e61852;
  margin: 0 25px !important;
  position: relative;
}

.swiper-container .swiper-pagination-bullet:before {
  content: '';
  width: 26px;
  height: 26px;
  display: block;
  border: 1px solid transparent;
  border-radius: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: all 0.3s;
}

.swiper-container .swiper-pagination-bullet-active:before {
  border: 1px solid #e61852;
}

