}

.modal .form__range__block {
  width: 710px;
  height: 55px;
  border-radius: 40px;
  border: solid 1px #ca2d4b;
  margin-bottom: 25px;
}

.modal .form__range__block .rangeslider {
  background: rgba(44, 50, 58, 0.15);
  border-radius: 0;
  box-shadow: none;
}

.modal .form__range__block .rangeslider--horizontal {
  height: 3px;
}

.modal .form__range__block .rangeslider__fill {
  background: #e61852;
  border-radius: 0;
  height: 5px;
  margin-top: -1.5px;
}

.modal .form__range__block .rangeslider__handle {
  width: 0;
  height: 0;
  border: none;
  top: 0;
}

.modal .form__range__block .rangeslider__handle:after {
  background: #e61852;
  border-radius: 100%;
  width: 20px;
  height: 20px;
  box-shadow: 0px 2px 7px 0 rgba(0, 0, 0, 0.45);
