  font-size: 15px;
  font-weight: bold;
  line-height: 1.47;
  letter-spacing: 0.4px;
  text-align: center;
  color: #2c323a;
  padding-bottom: 10px;
}

.modal .form__range__result span {
  font-size: 26px;
  font-weight: 500;
  line-height: 0.85;
  letter-spacing: 0.7px;
  text-align: center;
  color: #e61852;
  display: block;
}

.modal .form__range__result b {
  font-size: 12px;
  font-weight: normal;
  line-height: 1.5;
  letter-spacing: 0.3px;
  text-align: center;
  color: #2c323a;
  max-width: 390px;
  display: block;
  position: relative;
  left: 50%;
  transform: translate(-50%);
  margin-top: 30px;
