  border-radius: 17.5px;
  border: solid 1px rgba(44, 50, 58, 0.6);
  height: 35px;
  padding: 0 12px;
  text-align: center;
  width: 130px;
  position: relative;
  left: 50%;
  transform: translate(-50%);
}

.modal .form__radio input {
  display: none;
}

.modal .form__radio input + label p {
  font-size: 14px;
  line-height: 2.57;
  letter-spacing: 0.4px;
  text-align: left;
  color: #2c323a;
  display: flex;
  align-items: center;
  transition: all 0.3s;
}

@media only screen and (max-width: 1024px) {
  .modal .form__radio input + label p {
    font-size: 9px;
    width: 100%;
  }
}

.modal .form__radio input + label p:before {
  content: '';
  width: 6px;
  height: 6px;
  background: transparent;
  transition: all 0.3s;
  border-radius: 100px;
  border: 8px solid #ffffff;
  display: flex;
  box-sizing: content-box;
  box-shadow: 0 0 0 2px #e61852;
  margin-right: 10px;
}

@media only screen and (max-width: 1024px) {
  .modal .form__radio input + label p:before {
    height: 6px;
    width: 6px;
  }
}

.modal .form__radio input:checked + label p {
  color: #e61852;
}

.modal .form__radio input:checked + label p:before {
  background: #e61d55;
}

