.search__close {
  width: 30px;
  height: 30px;
  position: relative;
}

.search__close:hover span:after, .search__close:hover span:before {
  background: #e61852;
}

.search__close span {
  width: 30px;
  height: 30px;
  display: block;
  position: relative;
  margin-top: 13px;
}

.search__close span:before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  transform: rotate(45deg);
  width: 30px;
  height: 2px;
  background: #464646;
  transition: all 0.3s;
}

.search__close span:after {
  content: '';
  position: absolute;
  right: 0;
  top: 0;
  transform: rotate(-45deg);
  width: 30px;
  height: 2px;
  background: #464646;
  transition: all 0.3s;
}
