.footer {
  position: relative;
  z-index: 12;
  background: $greyish-brown;
  padding-top: 50px;
  .up {
    width: 50px;
    height: 50px;
    background-color: $lipstick-six;
    box-shadow: 0px 2px 8px 0 rgba(0, 0, 0, 0.15);
    border-radius: 100%;
    @include center;
    position: absolute;
    right: -50px;
    top: 50%;
    transform: translate(0, -50%);
    @include tr;
    @include r(1200) {
      right: 20px; }
    @include r(767) {
      display: none; }
    i {
      font-size: 20px;
      color: $white;
      transform: rotate(180deg); }
    &:hover {
      box-shadow: 0px 7px 30px 0 rgba(0, 0, 0, 0.25); } }
  &__nav {
    padding-bottom: 30px;
    border-bottom: 1px solid rgba($white, 0.15);
    &__logo {
      @include r(1024) {
        width: 130px; }
      @include r(767) {
        width: 100px;
        img {
          width: 100%; } } }
    h4 {
      margin-bottom: 5px;
      a {
        font-size: 16px;
        font-weight: 500;
        line-height: 2.5;
        letter-spacing: 0.4px;
        text-transform: uppercase;
        color: $white-85;
        @include tr;
        @include r(1024) {
          font-size: 11px; }
        @include r(767) {
          font-size: 10px; } }
      &:hover {
        a {
          color: $lipstick-six; } } }
    ul {
      margin-top: 25px;
      @include r(767) {
        margin-bottom: 20px; } }
    li {
      @include tr;
      a {
        font-size: 14px;
        line-height: 2.29;
        letter-spacing: 0.4px;
        color: $white-75;
        @include tr;
        @include r(1024) {
          font-size: 10px; }
        @include r(767) {
          font-size: 9px; } }
      &:hover {
        a {
          color: $lipstick-six; } } } }
  &__relations {
    padding: 30px 0;
    .container {
      position: relative; } }
  &__phone {
    a {
      font-size: 20px;
      letter-spacing: 0.5px;
      color: $white-five;
      padding-bottom: 15px;
      @include center-y;
      @include tr;
      @include r(1024) {
        font-size: 11px; }
      @include r(767) {
        font-size: 9px;
        justify-content: flex-end; }
      &:first-child {
        @include r(1024) {
          font-size: 15px; }
        @include r(767) {
          font-size: 13px; } }
      i {
        margin-right: 10px;
        display: inline-block; }
      &:hover {
        color: $lipstick-six; } } }
  &__social {
    ul {
      display: flex;
      justify-content: center;
      li {
        padding-right: 30px;
        &:last-child {
          padding-right: 0; }
        i {
          font-size: 22px;
          color: $white;
          @include tr;
          @include r(1024) {
            font-size: 19px; } }
        &:hover {
          i {
            color: $lipstick-six; } } } }
    p {
      font-size: 12px;
      line-height: 1.67;
      letter-spacing: 0.3px;
      color: $lipstick-six;
      padding-top: 15px;
      @include r(1024) {
        font-size: 9px; }
      span {
        color: $white; } } }
  &__logo-algoritm {
    padding-left: 25px;
    @include r(767) {
      margin-top: 30px; }
    img {
      @include r(1024) {
        width: 170px; } }
    p {
      font-size: 15px;
      font-weight: 300;
      line-height: 1.33;
      letter-spacing: 0.4px;
      color: $white-four;
      @include r(1024) {
        font-size: 12px; }
      a {
        color: $white; } } } }
