.fact {
  margin: 70px 0;
  @include r(1024) {
    margin: 30px 0; }
  .heading {
    h2 {
      @include r(767) {
        font-size: 16px; } } }
  &__block {
    display: inline-flex;
    margin-bottom: 50px;
    @include r(767) {
      flex-direction: column;
      align-items: center; }
    &:hover {
      .fact__block__img {
        box-shadow: 0px 3px 20px 0 rgba(0, 0, 0, 0.25);
        &:before {
          background: rgba(0, 0, 0, 0.3); } }
      h3 {
        color: $lipstick-five; } }
    &__img {
      overflow: hidden;
      width: 170px;
      height: 170px;
      margin-right: 30px;
      border-radius: 2px;
      position: relative;
      box-shadow: 0px 1px 10px 0 rgba(0, 0, 0, 0.25);
      @include r(1024) {
        width: 130px;
        height: 130px; }
      @include r(767) {
        margin-bottom: 20px;
        margin-right: 0; }
      &:before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        border-radius: 2px;
        background: transparent;
        @include tr; }
      img {
        width: 100%; } }
    &__text {
      flex: 1;
      h3 {
        font-size: 22px;
        font-weight: bold;
        letter-spacing: 0.6px;
        color: $dark;
        margin-bottom: 20px;
        @include tr;
        @include r(1024) {
          font-size: 15px; }
        @include r(767) {
          text-align: center;
          max-width: 230px;
          margin: 0 auto 15px; } }
      p {
        font-size: 15px;
        line-height: 1.47;
        letter-spacing: 0.4px;
        color: rgba(39, 45, 54, 0.75);
        @include r(1024) {
          font-size: 13px; }
        @include r(767) {
          text-align: center;
          font-size: 12px; } } } } }
