.catalog-card {
  padding-top: 90px;
  background-color: #fbfbfb;
  overflow: hidden;
  .home-hit__swiper {
    padding-bottom: 100px;
    background: $white;
    padding-top: 120px;
    @include r(767) {
      padding-top: 30px; }
    .swiper-button-next {
      @include r(767) {
        right: -20px; } }
    .swiper-button-prev {
      @include r(767) {
        left: -20px; } } }
  .heading {
    justify-content: flex-start;
    h2 {
      line-height: 1;
      text-align: left; } }
  &__block {
    display: flex;
    justify-content: space-between;
    @include r(767) {
      flex-direction: column; } }
  &__img {
    @include r(767) {
      @include center;
      margin-bottom: 20px; }
    img {
      max-width: 360px;
      @include r(767) {
        height: 193px; } } }
  &__size {
    display: flex;
    margin-bottom: 85px;
    max-width: 370px;
    @include r(767) {
      width: 100%;
      align-items: center; }
    p {
      font-size: 16px;
      line-height: 1.13;
      letter-spacing: 0.4px;
      color: $dark;
      flex: 1;
      @include r(767) {
        font-size: 12px; } }
    span {
      border-radius: 20px;
      border: solid 1px $dark;
      font-size: 18px;
      font-weight: 500;
      letter-spacing: 0.5px;
      color: $dark;
      @include center-y;
      padding-left: 15px;
      width: 200px;
      height: 40px;
      position: relative;
      cursor: pointer;
      @include tr;
      &.active {
        border-bottom-left-radius: 0px;
        border-bottom-right-radius: 0px; }
      @include r(767) {
        font-size: 13px;
        width: 155px; }
      i {
        font-size: 12px;
        position: absolute;
        right: 15px;
        @include tr;
        &.active {
          transform: rotate(180deg); } } }
    &__list {
      display: none;
      position: absolute;
      top: 39px;
      left: 50%;
      transform: translate(-50%);
      width: 200px;
      background: $white;
      border: 1px solid #000;
      border-top: none;
      border-bottom-left-radius: 20px;
      border-bottom-right-radius: 20px;
      background: #fbfbfb;
      li {
        padding: 10px;
        border-top: 1px solid #000;
        a {
          font-size: 18px;
          font-weight: 500;
          letter-spacing: 0.5px;
          color: $dark;
          @include tr; }
        &:first-child {
          border-top: none; }
        &:hover {
          a {
            color: $lipstick; } } } } }
  &__table {
    max-width: 370px;
    border-radius: 25px;
    background-color: $white;
    box-shadow: 0px 2px 10px 0 rgba(0, 0, 0, 0.35);
    padding: 70px 30px;
    @include r(1024) {
      padding: 30px;
      height: 100%; }
    h4 {
      font-size: 22px;
      font-weight: bold;
      line-height: 1.36;
      letter-spacing: 0.6px;
      text-align: center;
      color: $lipstick-three;
      margin-bottom: 50px;
      @include r(1024) {
        font-size: 20px;
        margin-bottom: 20px; } }
    tr {
      border-bottom: 1px solid rgba($dark, 0.15);
      &:first-child {
        border-bottom: none;
        th {
          padding-bottom: 10px; } } }
    th {
      font-size: 22px;
      font-weight: 500;
      line-height: 1.36;
      letter-spacing: 0.6px;
      text-align: center;
      color: $dark;
      @include r(1024) {
        font-size: 21px; }
      &:first-child {
        text-align: left; }
      &:last-child {
        text-align: right; } }
    td {
      font-size: 18px;
      font-weight: 500;
      line-height: 2.78;
      letter-spacing: 0.5px;
      text-align: center;
      @include r(1024) {
        font-size: 18px; }
      color: $dark;
      &:first-child {
        text-align: left; }
      &:last-child {
        text-align: right; } } }
  .block__img {
    float: right;
    width: 345px;
    margin-left: 30px;
    margin-bottom: 90px;
    @include r(767) {
      width: 165px;
      height: 165px;
      float: none;
      margin: 20px auto; }
    &__content {
      @include r(767) {
        height: 100%; }
      img {
        width: calc(100% - 30px);
        position: relative;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%); } } }
  &__info {
    padding: 120px 0;
    position: relative;
    z-index: 1;
    @include r(1024) {
      padding: 50px 15px; }
    .heading {
      margin-bottom: 40px; }
    h2 {
      padding-bottom: 10px;
      a {
        color: $lipstick-five; } }
    p {
      font-size: 15px;
      line-height: 1.47;
      letter-spacing: 0.4px;
      color: $dark-three;
      padding-bottom: 30px;
      a {
        font-weight: 500;
        color: $lipstick-five; } } } }
