.blocker {
  background: rgba(#fafafa, 0.9); }
.calculator {
  display: none; }
.modal {
  max-width: 825px;
  width: 100%;
  border-radius: 25px;
  background-color: #fafafa;
  box-shadow: 0px 5px 32px 0 rgba(0, 0, 0, 0.25);
  padding: 120px 55px;
  h2 {
    font-size: 48px;
    font-weight: bold;
    line-height: 1.17;
    letter-spacing: 1.2px;
    text-align: center;
    color: #e61852;
    margin-bottom: 60px; }
  h4 {
    font-size: 30px;
    font-weight: bold;
    line-height: 1.87;
    letter-spacing: 0.8px;
    text-align: center;
    color: #2c323a;
    margin-bottom: 40px; }
  .form {
    &__range {
      &__block {
        width: 710px;
        height: 55px;
        border-radius: 40px;
        border: solid 1px #ca2d4b;
        margin-bottom: 25px;
        .rangeslider {
          background: rgba(#2c323a, 0.15);
          border-radius: 0;
          box-shadow: none; }
        .rangeslider--horizontal {
          height: 3px; }
        .rangeslider__fill {
          background: #e61852;
          border-radius: 0;
          height: 5px;
          margin-top: -1.5px; }
        .rangeslider__handle {
          width: 0;
          height: 0;
          border: none;
          top: 0;
          &:after {
            background: #e61852;
            border-radius: 100%;
            width: 20px;
            height: 20px;
            box-shadow: 0px 2px 7px 0 rgba(0, 0, 0, 0.45); } } }
      &__result {
        margin-bottom: 55px;
        p {
          font-size: 15px;
          font-weight: bold;
          line-height: 1.47;
          letter-spacing: 0.4px;
          text-align: center;
          color: #2c323a;
          padding-bottom: 10px; }
        span {
          font-size: 26px;
          font-weight: 500;
          line-height: 0.85;
          letter-spacing: 0.7px;
          text-align: center;
          color: #e61852;
          display: block; }
        b {
          font-size: 12px;
          font-weight: normal;
          line-height: 1.5;
          letter-spacing: 0.3px;
          text-align: center;
          color: #2c323a;
          max-width: 390px;
          display: block;
          position: relative;
          left: 50%;
          transform: translate(-50%);
          margin-top: 30px; } } }
    &__block-application {
      p {
        font-size: 15px;
        font-weight: bold;
        line-height: 1.47;
        letter-spacing: 0.4px;
        text-align: center;
        color: #2c323a; }
      input {
        border-radius: 17.5px;
        border: solid 1px rgba(#2c323a, 0.6);
        height: 35px;
        padding: 0 12px;
        text-align: center;
        width: 130px;
        position: relative;
        left: 50%;
        transform: translate(-50%); } }
    &__radio {
      input {
        display: none;
        & + label {
          p {
            font-size: 14px;
            line-height: 2.57;
            letter-spacing: 0.4px;
            text-align: left;
            color: #2c323a;
            @include center-y;
            @include tr;
            @include r(1024) {
              font-size: 9px;
              width: 100%; }
            &:before {
              content: '';
              width: 6px;
              height: 6px;
              background: transparent;
              @include tr;
              border-radius: 100px;
              border: 8px solid $white;
              display: flex;
              box-sizing: content-box;
              box-shadow: 0 0 0 2px $lipstick-five;
              margin-right: 10px;
              @include r(1024) {
                height: 6px;
                width: 6px; } } } }
        &:checked + label p {
          color: #e61852;
          &:before {
            background: $lipstick-seven; } } } } } }



.search {
  position: absolute;
  top: -200px;
  width: 100%;
  height: 170px;
  background: $white;
  z-index: 10;
  box-shadow: 0px 2px 12px 0 rgba(0, 0, 0, 0.1);
  @include tr;
  @include r(767) {
    height: 100px; }
  &.active {
    top: 0; }
  &__block {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100%;
    .form {
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 100%;
      padding-right: 50px;
      @include r(767) {
        padding-right: 10px; }
      input[type=text] {
        width: 100%;
        font-size: 58px;
        color: $charcoal-grey-two;
        @include placeholder-color($charcoal-grey-two);
        @include r(767) {
          font-size: 24px; } }
      input[type=submit] {
        @include r(767) {
          font-size: 16px;
          height: 35px;
          padding: 0 15px;
          text-transform: none; } } } }
  &__close {
    width: 30px;
    height: 30px;
    position: relative;
    &:hover {
      span {
        &:after, &:before {
          background: $lipstick-five; } } }
    span {
      width: 30px;
      height: 30px;
      display: block;
      position: relative;
      margin-top: 13px;
      &:before {
        content: '';
        position: absolute;
        left: 0;
        top: 0;
        transform: rotate(45deg);
        width: 30px;
        height: 2px;
        background: $greyish-brown;
        @include tr; }
      &:after {
        content: '';
        position: absolute;
        right: 0;
        top: 0;
        transform: rotate(-45deg);
        width: 30px;
        height: 2px;
        background: $greyish-brown;
        @include tr; } } } }
