.services {
  padding-bottom: 100px;
  @include r(1024) {
    padding: 0 0 40px; }
  &__block {
    border-top: 1px solid rgba($dark, 0.15);
    padding-top: 85px;
    padding-bottom: 45px;
    @include r(1024) {
      padding: 40px 0; }
    &:first-child {
      border: none; }
    .heading {
      margin: 0;
      margin-bottom: 35px;
      justify-content: flex-start; }
    p {
      font-size: 15px;
      line-height: 1.47;
      letter-spacing: 0.4px;
      color: $dark-three;
      padding-bottom: 20px;
      a {
        font-weight: 500;
        color: #df1f59; } }
    ul {
      margin-bottom: 20px; }
    li {
      font-size: 14px;
      line-height: 1.57;
      letter-spacing: 0.4px;
      color: $dark-three; } }
  .block__img {
    width: 545px;
    height: 345px;
    float: left;
    margin-bottom: 30px;
    margin-right: 30px;
    &-secondary {
      float: right;
      margin-left: 30px;
      margin-right: 0; }
    &__content {
      height: 100%; }
    @include r(1024) {
      height: 220px;
      width: 345px;
      margin-top: 50px;
      margin-bottom: 70px; }
    @include r(767) {
      width: 275px;
      height: 175px; } }
  &__phone {
    @include center;
    margin-bottom: 60px;
    a {
      font-size: 80px;
      letter-spacing: 2px;
      background-image: linear-gradient(345deg, #8016a7, #e01f59), linear-gradient($dark-three, $dark-three);
      background-clip: content-box;
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      @include r(767) {
        font-size: 24px; } } } }
